    @Input() emptyMessage = 'ææ æ°æ®';

    /**
     * åå»è¡éä¸­åï¼å¨æ¬¡ç¹å»ä¸ä¼è¢«åæ¶éä¸­ç¶æ;
     */
    @Input() keepSelect = true;
    /** èæå è½½ */
    @Input() virtualized = false;
    /**
     * ä»åè®¸éæ©å¶å­èç¹
     * - default: åºç¨æå¡å¨ç«¯ä¼ åçè®¾ç½®ï¼å¦ææå¡å¨ç«¯æ²¡æè®¾ç½®ï¼åä¸ no ä¿æä¸è³ï¼
     * - yes: å¦æå®¢æ·ç«¯è®¾ç½®ä¸º `yes`, æå¡å¨ç«¯çè®¾ç½®å¤±æï¼
     * - no: åè®¸éæ©ææèç¹ï¼æå¡å¨ç«¯è®¾ç½®å¤±æ
     */
    @Input() onlySelectLeaf: boolean | 'default' | 'yes' | 'no' = 'default';

